import { Component, Event, EventEmitter, Host, Prop, h } from '@stencil/core';
import { TPositions } from '@components/ui/ir-toast/toast';

export type ToastVariant = 'info' | 'success' | 'warning' | 'danger';

@Component({
  tag: 'ir-toast-alert',
  styleUrl: 'ir-toast-alert.css',
  shadow: true,
})
export class IrToastAlert {
  /** Unique identifier passed back to the provider when interacting with the toast */
  @Prop() toastId!: string;

  /** Heading displayed at the top of the toast */
  @Prop() label?: string;

  /** Plain text description for the toast body */
  @Prop() description?: string;

  /** Maps to visual style tokens */
  @Prop() variant: ToastVariant = 'info';

  /** Whether the close button should be rendered */
  @Prop() dismissible = true;

  /** Optional primary action label */
  @Prop() actionLabel?: string;

  /** Indicates when the provider is playing the exit animation */
  @Prop() leaving = false;

  /** Toast position drives enter/exit direction */
  @Prop() position: TPositions = 'top-right';

  @Event() irToastDismiss: EventEmitter<{ id: string; reason: 'manual' }>;
  @Event() irToastAction: EventEmitter<{ id: string }>;
  @Event() irToastInteractionChange: EventEmitter<{ id: string; interacting: boolean }>;

  private handleAction = () => {
    this.irToastAction.emit({ id: this.toastId });
  };

  private handleDismiss = () => {
    if (!this.dismissible) {
      return;
    }
    this.irToastDismiss.emit({ id: this.toastId, reason: 'manual' });
  };

  private setInteracting = (interacting: boolean) => {
    this.irToastInteractionChange.emit({ id: this.toastId, interacting });
  };

  private getIcon() {
    switch (this.variant) {
      case 'success':
        return (
          <svg viewBox="0 0 24 24" class="toast__icon" aria-hidden="true">
            <path d="M12 22C6.49 22 2 17.51 2 12S6.49 2 12 2s10 4.49 10 10-4.49 10-10 10zm-1.2-5.2l6.3-6.3-1.4-1.4-4.9 4.9-2.1-2.1-1.4 1.4 3.5 3.5z"></path>
          </svg>
        );
      case 'warning':
        return (
          <svg viewBox="0 0 24 24" class="toast__icon" aria-hidden="true">
            <path d="M12 2 1 21h22L12 2zm0 5 6.46 12H5.54L12 7zm-1 4v4h2v-4h-2zm0 6v2h2v-2h-2z"></path>
          </svg>
        );
      case 'danger':
        return (
          <svg viewBox="0 0 24 24" class="toast__icon" aria-hidden="true">
            <path d="M12 2C6.48 2 2 6.48 2 12s4.48 10 10 10 10-4.48 10-10S17.52 2 12 2zm1 15h-2v-2h2v2zm0-4h-2V7h2v6z"></path>
          </svg>
        );
      default:
        return (
          <svg viewBox="0 0 24 24" class="toast__icon" aria-hidden="true">
            <path d="M12 2a10 10 0 1 0 .001 20.001A10 10 0 0 0 12 2zm1 15h-2v-2h2v2zm0-4h-2V7h2v6z"></path>
          </svg>
        );
    }
  }

  render() {
    return (
      <Host
        role="status"
        aria-live="polite"
        aria-atomic="true"
        data-variant={this.variant}
        data-leaving={this.leaving ? 'true' : 'false'}
        data-position={this.position}
        onMouseEnter={() => this.setInteracting(true)}
        onMouseLeave={() => this.setInteracting(false)}
        onFocusin={() => this.setInteracting(true)}
        onFocusout={() => this.setInteracting(false)}
      >
        <article class="toast" part="base">
          <div class="toast__leading" part="icon">
            {this.getIcon()}
          </div>

          <div class="toast__body" part="content">
            {this.label && (
              <p class="toast__title" part="title">
                {this.label}
              </p>
            )}
            {this.description && (
              <p class="toast__description" part="description">
                {this.description}
              </p>
            )}
          </div>

          {(this.actionLabel || this.dismissible) && (
            <div class="toast__actions" part="actions">
              {this.actionLabel && (
                <button type="button" class="toast__action" onClick={this.handleAction}>
                  {this.actionLabel}
                </button>
              )}
              {this.dismissible && (
                <button type="button" class="toast__dismiss" aria-label="Dismiss notification" onClick={this.handleDismiss}>
                  <svg viewBox="0 0 16 16" aria-hidden="true">
                    <path d="M4.646 4.646 8 8l3.354-3.354 1.292 1.292L9.292 9.293l3.354 3.354-1.292 1.292L8 10.707l-3.354 3.354-1.292-1.292L6.708 9.293 3.354 5.939z"></path>
                  </svg>
                </button>
              )}
            </div>
          )}
        </article>
      </Host>
    );
  }
}
